.grid {
  border-spacing: 0;
  border: 1px solid #999;
  width: 100%;
  border-collapse: collapse;
}

.grid-app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.grid-field {
  cursor: default;
  text-align: center;
}

.grid-field input {
  width: 100%;
  height: 100%;
  border: none;
  text-align: center;
  cursor: pointer;
  color: #555;
}

.grid-cell {
  margin-left: 10px;
}

.grid-field input:focus {
  border: none;
  cursor: text;
  outline: none;
}

.focused {
  -webkit-box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
  border: 2px solid #40a9ff;
}

.inFocusList {
  background-color: #ffeaea;
}

.grid-field input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #c5c7c6;
}
.grid-field input::-moz-placeholder { /* Firefox 19+ */
  color: #c5c7c6;
}
.grid-field input:-ms-input-placeholder { /* IE 10+ */
  color: #c5c7c6;
}
.grid-field input:-moz-placeholder { /* Firefox 18- */
  color: #c5c7c6;
}

.grid-td-index {
  background-color: #e8e8e873;
  font-size: 0.875rem;
  color: #444;
  font-weight: bold;
  text-align: center;
  border: 1px solid #999;
}

.grid-title {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Notes .formula {
  font-size: 0.6rem;
}

.Notes {
  display: -ms-flexbox;
  display: flex;
  padding: 5px 30px;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 13px;
}

.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 15px;
  background-color: #e8e8e873;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid #999;
  border-bottom: none !important;
}

.input-container input {
  margin: 7px 0 7px 14px;
  padding: 3px;
  outline: 0;
}

.input-container input:focus {
  border: 2px solid #40a9ff;
  outline: 0;
  -webkit-box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}

td:first-child {
  width: 3%
}

td {
  border: 1px solid #999;
  color: #555;
  padding: 4px;
}

tr:first-child td {
  background-color: #e8e8e873;
  text-align: center;
  font-weight: bold;
}


